@media (prefers-color-scheme: dark) {
  body {
    background-color: #2c2c3b;
  }

  .tagline,
.formulario label,
.login .acciones a,
.crear .acciones a,
.olvide .acciones a {
    color: #FFFFFF;
  }

  .descripcion-pagina,
.formulario .campo input,
.nombre-pagina {
    color: #c7ccd5;
  }

  .formulario .campo input {
    background-color: #4d4d67;
  }

  .alerta.error {
    background-color: #FFFFFF;
  }

  .barra-mobile {
    background-color: #1d1d27;
  }

  .filtros {
    background-color: #424258;
  }
  .filtros h2,
.filtros label {
    color: #FFFFFF;
  }
  .filtros input[type=radio] {
    accent-color: #F59E0B;
  }

  .dashboard {
    background-color: #2c2c3b;
  }
  .dashboard .enlace {
    color: #f3f4f6;
  }
  .dashboard .enlace:hover {
    color: #b9bfca;
  }

  .listado-proyectos .proyecto {
    background-color: #585875;
  }
  .listado-proyectos .proyecto:hover {
    background-color: #4d4d67;
  }

  .listado-tareas .tarea {
    background-color: #424258;
    border: none;
  }
  .listado-tareas .tarea p {
    color: #f3f4f6;
  }
}